@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
*{box-sizing:border-box}body,h1,h2,h3,h4,p,li,ul{margin:0;padding:0}li{list-style:none}

.App{padding:40px 0;height:100vh;font-family:'Jost', sans-serif;color:#664B2A;background:center/contain repeat url(../../static/media/crello-369560084-stock-photo-top-view-fresh-buns-beige.ee1dd6bd.jpeg)}.App__container{margin:0 auto;padding:20px;width:600px;position:relative;background-color:#e9d6beab}.App__titleInfo{margin-bottom:20px}.App__title{font-size:24px}.App__list{display:flex;flex-direction:column;grid-gap:12px;gap:12px}.App__item{padding:0 20px;display:flex;justify-content:space-between;align-items:center}.App__button{padding:4px 0;width:80px;border-radius:10px;border-color:transparent;background-color:#fff;font-family:inherit;cursor:pointer;transition:color, background-color 0.3s}.App__button:hover{color:white;background-color:#664B2A}.App__button--clear{position:absolute;top:32px;right:20px;width:32px}

